import SurrogateFrameDataManager, {SurrogateFrameType, SkipFrame} from './SurrogateFrameDataManager';

type TransformStreamPostMessage = {
  options: {
    kind: string;
    codec: string;
  };
  readable: ReadableStream;
  writable: WritableStream;
};

type ForceInvalidFramePostMessage = {
  forceInvalidFrame: boolean;
};

interface IResponseWorker {
  onmessage: (message: MessageEvent<ForceInvalidFramePostMessage>) => void;
}

const worker: IResponseWorker = self as IResponseWorker;

worker.onmessage = event => {
  WorkerTransformStream.forceInvalidFrame = event.data.forceInvalidFrame;
};

if (self.RTCTransformEvent) {
  self.onrtctransform = event => {
    const transformer = event.transformer as TransformStreamPostMessage;

    WorkerTransformStream.codec = transformer.options.codec;
    WorkerTransformStream.transformFunction = transformer.options.kind === 'video' ? WorkerTransformStream.videoTransformFunction : WorkerTransformStream.audioTransformFunction;
    WorkerTransformStream.forceInvalidFrame = false;

    const transformStream = new TransformStream({transform: WorkerTransformStream.transformFunction});
    const source = transformer.readable;
    const sink = transformer.writable;

    source
      .pipeThrough(transformStream)
      .pipeTo(sink);
  };
}

class WorkerTransformStream {
  static codec: string;
  static transformFunction: TransformerTransformCallback<AudioData | VideoFrame, TransformStreamDefaultController> | undefined;
  static forceInvalidFrame: boolean;

  static videoTransformFunction(chunk, controller): void {
    postMessage({
      chunk: {
        type: chunk.type,
        timestamp: chunk.timestamp,
        data: chunk.data
      }
    });

    if (WorkerTransformStream.forceInvalidFrame) {
      let surrogate: SurrogateFrameType;

      WorkerTransformStream.forceInvalidFrame = false;

      if (chunk.type === 'key') {
        surrogate = SurrogateFrameDataManager.getInvalidSurrogateVideoIFrame(WorkerTransformStream.codec);
      } else {
        surrogate = SurrogateFrameDataManager.getInvalidSurrogateVideoPFrame(WorkerTransformStream.codec);
      }

      if (surrogate === SkipFrame) {
        return;
      }

      chunk.data = surrogate || chunk.data;
    } else {
      if (chunk.type === 'key') {
        chunk.data = SurrogateFrameDataManager.getSurrogateVideoIFramePerCodec(WorkerTransformStream.codec) || chunk.data;
      } else {
        chunk.data = SurrogateFrameDataManager.getSurrogateVideoPFramePerCodec(WorkerTransformStream.codec) || chunk.data;
      }
    }

    controller.enqueue(chunk);
  }

  static audioTransformFunction(chunk, controller): void {
    postMessage({
      chunk: {
        timestamp: chunk.timestamp,
        data: chunk.data
      }
    });

    if (WorkerTransformStream.forceInvalidFrame) {
      WorkerTransformStream.forceInvalidFrame = false;
      chunk.data = SurrogateFrameDataManager.getInvalidSurrogateAudioData() || chunk.data;
    } else {
      chunk.data = SurrogateFrameDataManager.getSurrogateAudioSilentPerCodec(WorkerTransformStream.codec) || chunk.data;
    }

    controller.enqueue(chunk);
  }

  private constructor() {
    throw new Error('WorkerTransformStream is a static class that may not be instantiated');
  }
}

export {};